import React, { Component } from 'react';
import { Avatar, Drawer, Divider } from 'antd';
import LocationData from "assets/data/district.json"
import { 
	MobileOutlined, 
	CarryOutOutlined, 
	UserOutlined, 
	CompassOutlined,
	IdcardOutlined,
	FolderOpenOutlined,
	LoadingOutlined,
	ClockCircleOutlined,
	BgColorsOutlined
} from '@ant-design/icons';

export class UserView extends Component {
	state = {
		provinceData : LocationData
	}
	render() {
		const { data, visible, close, site, sitsLoading, allUser, allUserLoading} = this.props;
		const { provinceData } = this.state;
		return (
			<Drawer
				width={300}
				placement="right"
				onClose={close}
				closable={false}
				visible={visible}
			>
				<div className="text-center mt-3">
					<h3 className="mt-2 mb-0">{data?.name}</h3>
					<span className="text-muted">{(!sitsLoading && data) ? (site[data.site] ? site[data.site].siteId : data.backupSiteId)+''+data.clientId:(<LoadingOutlined/>)}</span>
				</div>
				<Divider dashed />
				<div className="">
					<h6 className="text-muted text-uppercase mb-3">CONTACT</h6>
					<p>
						<MobileOutlined />
						<span className="ml-3 text-dark">{data?.phoneNumber? '0'+data?.phoneNumber: '-'}</span>
					</p>
					<p>
						<IdcardOutlined />
						<span className="ml-3 text-dark">{data?.idNumber}</span>
					</p>
					
				</div>
				<div className="mt-5">
					<h6 className="text-muted text-uppercase mb-3">Address</h6>
					<p>
						<CompassOutlined />
						<span className="ml-3 text-dark">{data?.district? provinceData.districts[data.district].name + ' District' : '-'}</span>
					</p>
					<p>
						<CompassOutlined />
						<span className="ml-3 text-dark">{data?.sector? provinceData.districts[data.district].sectors[data.sector].name + ' Sector' : '-'}</span>
					</p>
					<p>
						<CompassOutlined />
						<span className="ml-3 text-dark">{data?.cell? provinceData.districts[data.district].sectors[data.sector].cells[data.cell].name + ' Cell' : '-'}</span>
					</p>
					<p>
						<CompassOutlined />
						<span className="ml-3 text-dark">{data?.village? provinceData.districts[data.district].sectors[data.sector].cells[data.cell].villages[data.village].name + ' Village': '-'}</span>
					</p>
				</div>
				<div className="mt-5">
					<h6 className="text-muted text-uppercase mb-3">Client details</h6>
					<p>
						<CarryOutOutlined />
						<span className="ml-3 text-dark">Joined: {data ? new Date(data.dateJoined).toLocaleDateString()	: null}</span>
					</p>
					<p>
						<BgColorsOutlined />
						<span className="ml-3 text-dark">Meter: {data ? data.meter : null}</span>
					</p>
					<p>
						<FolderOpenOutlined />
						<span className="ml-3 text-dark">Site: {data ? (!sitsLoading ? (site[data.site] ? site[data.site].name : data.backupSiteName):(<LoadingOutlined/>)) : null}</span>
					</p>
					
					<p>
						<UserOutlined />
						<span className="ml-3 text-dark">By: {data ? (!allUserLoading ? (allUser[data.userId] ? allUser[data.userId].name : data.backupUser) : (<LoadingOutlined/>)) : null}</span>
					</p>
					<p>
						<ClockCircleOutlined />
						<span className="ml-3 text-dark">On: {data ? new Date(parseInt(data._id)).toLocaleString():null}</span>
					</p>
				</div>
			</Drawer>
		)
	}
}

export default UserView
