import React, {useState} from 'react'
import moment from "moment";
import { Row, Col, Card, Form, DatePicker, Modal, InputNumber, Select, Button, Input } from 'antd';
import Flex from 'components/shared-components/Flex'
import { Typography } from "antd";
import { EditOutlined, DeleteOutlined, PlusCircleOutlined } from '@ant-design/icons';

const { Title } = Typography;
const { Option } = Select;

const rules = {
	departureAddress: [
		{
			required: true,
			message: 'Select departure location',
		}
	],
	destinationAddress: [
		{
			required: true,
			message: 'Select destination location'
		}
	],
	driver: [
		{
			required: true,
			message: 'Select trip driver',
		}
	],
	truck: [
		{
			required: true,
			message: 'Select truck to be used'
		}
	],
	milleage: [
		{
			required: true,
			message: 'Please enter milleage given',
		}
	],
	expectedReturn: [
		{
			required: true,
			message: 'Expected date for returning',
		}
	]
}

const GeneralField = props => {
	const {employees, employeesLoading, trucks, trucksLoading, destinations, destinationsLoading, borders, bordersLoading} = props;
	const [visible, setVisible] = useState(false);
  	const [confirmLoading, setConfirmLoading] = useState(false);
	
	const disabledDate = (current) => {
		let response = false;
		if(current && (current < moment().startOf("day"))) response = true;
		return current && response;
	}
	const [form] = Form.useForm();


	const showModal = () => {
		setVisible(true);
	};

	const handleOk = async e => {
		
		try {
		  const values = await form.validateFields();
		  let border = values.border.slice(0,1) === ' ' ? values.border.slice(1) : values.border;
		  let amount = values.amount.slice(0,1) === ' ' ? values.amount.slice(1) : values.amount;
		  if (border==='' || amount==='') return;
		  setConfirmLoading(true);
		  let storeData = { border,amount }
		  //DataService.create(storeData)
		  setVisible(false);
		  setConfirmLoading(false);
		  form.setFieldsValue({border:null,amount:null});
		} catch (errInfo) {}
		
	};

	const handleCancel = () => {
		setVisible(false);
		form.setFieldsValue({border:null,amount:null});
	};
	

	return (
		
	<Row gutter={16}>
		
		<Col xs={24} sm={24} md={17}>
			<Card>
			<Row gutter={16}>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="departureAddress" label="Departure Address" rules={rules.departureAddress}>
							<Select
									className="w-100"
									showSearch
									placeholder="Select departure"
									optionFilterProp="children"
								>
								{!destinationsLoading && destinations.docs.map((truck, index) => (
									<Option key={index} value={truck.data()._id}>
										{truck.data().name+' - '+truck.data().country}
									</Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="destinationAddress" label="Destination Address" rules={rules.destinationAddress}>
							<Select
									className="w-100"
									showSearch
									placeholder="Select destination"
									optionFilterProp="children"
								>
								{!destinationsLoading && destinations.docs.map((truck, index) => (
									<Option key={index} value={truck.data()._id}>
										{truck.data().name+' - '+truck.data().country}
									</Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="driver" label="Driver" rules={rules.driver}>
							<Select
									className="w-100"
									showSearch
									placeholder="Select a driver"
									optionFilterProp="children"
								>
								{!employeesLoading && employees.docs.filter(x=> x.data().type == "Driver").map((employee, index) => (
									<Option key={index} value={employee.data()._id}>
										{employee.data().name+' - '+employee.data().phoneNumber}
									</Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="truck" label="Truck" rules={rules.truck}>
							<Select
									className="w-100"
									showSearch
									placeholder="Select a truck"
									optionFilterProp="children"
								>
								{!trucksLoading && trucks.docs.map((truck, index) => (
									<Option key={index} value={truck.data()._id}>
										{truck.data().model+' - '+truck.data().plateNumber}
									</Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="expectedReturn" label="Expected return" rules={rules.expectedReturn}>
							<DatePicker className="w-100" format="DD-MM-YYYY" disabledDate={disabledDate}/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="milleage" label="Milleage amount" rules={rules.milleage}>
							<InputNumber
								className="w-100"
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
                      			formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							/>
						</Form.Item>
					</Col>
				</Row>
				
			</Card>
		</Col>
		<Col xs={24} sm={24} md={7}>
			<Card>
				<Flex className="mb-1" mobileFlex={false} justifyContent="between" alignItems="center">
					<div className="mr-md-3 mb-3">
						<Title level={4}>Road Tall</Title>
					</div>
					<div>
						<Button onClick={showModal} size="small" type="primary" icon={<PlusCircleOutlined />} block>Add New</Button>
					</div>
				</Flex>
			</Card>
			<Modal
              title="New Road Tall details"
              visible={visible}
              onOk={handleOk}
              confirmLoading={confirmLoading}
              onCancel={handleCancel}
            >
              <Row gutter={16}>
                <Col xs={24} sm={24} md={24}>
                  <Form.Item name="roadBorder" label="Border name" rules={[
                    {
                      required: true,
                      message: `border is required.`,
                    },
                  ]}>
                    <Select
							className="w-100"
							showSearch
							placeholder="Select border"
							optionFilterProp="children"
						>
						{!bordersLoading && borders.docs.map((border, index) => (
							<Option key={index} value={border.data()._id}>
								{border.data().name+' - '+border.data().country}
							</Option>
						))}
					</Select>
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={24}>
					<Form.Item name="roadAmount" label="Amount paid" rules={[
                    {
                      required: true,
                      message: `enter amount paid on border`,
                    }]}>
						<InputNumber
							className="w-100"
							placeholder="Enter amount"
							parser={value => value.replace(/\$\s?|(,*)/g, '')}
							formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
						/>
					</Form.Item>
                </Col>
              </Row>
            </Modal> 
		</Col>
	</Row>
)}

export default GeneralField
