import React, {useState} from 'react'
import { Card, Table, Button, Tag, message, Menu, Modal, Avatar } from 'antd';
import UserView from './UserView';
import { EditOutlined, DeleteOutlined, PlusCircleOutlined, LoadingOutlined } from '@ant-design/icons';
import AvatarStatus from 'components/shared-components/AvatarStatus';
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown';
import NumberFormat from 'react-number-format';
import Flex from 'components/shared-components/Flex'
import { useHistory } from "react-router-dom";
import { DataService } from "services/milleage.service";
import { Typography } from "antd";
import utils from 'utils';

const { Title } = Typography;
const { confirm } = Modal;
const getUserStatus = status => {
	if(status) {
		return <Tag className ="text-capitalize" color='cyan'>Active</Tag>
	}
	else{
		return <Tag className ="text-capitalize" color='red'>Blocked</Tag>
	}
}

const ClientList = ({systemData}) => {
	let history = useHistory();
	const [userProfileVisible, setUserProfileVisible] = useState(false)
	const [selectedUser, setSelectedUser] = useState(null)
	const { avatarColors, milleages, milleagesLoading, site, sitsLoading, allUser, allUserLoading, unpaidBalance } = systemData;
	const dropdownMenu = row => (
		<Menu>
			<Menu.Item onClick={() => {viewDetails(row)}}>
				<Flex alignItems="center">
					<EditOutlined />
					<span className="ml-2">Edit Details</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => showConfirm(row)}>
				<Flex alignItems="center">
					<DeleteOutlined />
					<span className="ml-2">Delete</span>
				</Flex>
			</Menu.Item>
		</Menu>
	);
	
	const addMilleage = () => {
		history.push(`/app/apps/milleage/add`)
	}
	
	const viewDetails = row => {
		history.push(`/app/apps/milleage/edit/${row._id}`)
	}
	const handleMenuClick = data => {
		let status = data.status && data.status === true ? false : true;
		let displayData = status ? data.name + ' Activate' : data.name +' Blocked';
		data.status = status;
		DataService.update(data._id,data);
		message.success(`Milleage created ${displayData}`);
		closeUserProfile();
	};
	const showUserProfile = userInfo => {
		setUserProfileVisible(true)
		setSelectedUser(userInfo)
	};
	
	const closeUserProfile = () => {
		setUserProfileVisible(false)
		setSelectedUser(null)
	}
	const deleteRow = row => {
		row.status = false;
		DataService.update(row._id,row);
		message.success({ content: `Milleage ${row.name} deleted `, duration: 3 });
	}

	function showConfirm(row) {
		confirm({
		  title: 'Do you want to delete this milleage?',
		  onOk() {
			deleteRow(row)
		  },
		  onCancel() {},
		});
	  }

	const tableColumns = [
		{
			title: 'ID',
			dataIndex: 'id',
			render: (_, record) => (
				<div onClick={() => {showUserProfile(record.data())}} style={{cursor:'pointer'}}>
					{!sitsLoading ? (site[record.data().site] ? site[record.data().site].siteId : record.data().backupSiteSiteId)+''+record.data().clientId:(<LoadingOutlined/>)}
				</div>)
		},
		{
			title: 'Client',
			dataIndex: 'client',
			render: (_,record) => (
				<div className="d-flex" style={{cursor:'pointer'}} onClick={() => {showUserProfile(record.data())}}>
					<AvatarStatus size={30} type="square" src={
						<Avatar size={30} className="font-size-sm" style={{backgroundColor: avatarColors[Math.floor(Math.random() * avatarColors.length) - 1]}}>
						  {utils.getNameInitial(record.data().name)}
						</Avatar>} name={record.data().name} subTitle={record.data().meter}/>
				</div>
			  )
		},
		{
			title: 'Type',
			dataIndex: 'type',
			render: (_, record) => record.data().type === 1 ? "Individual" : (record.data().type === 2 ?  "Organization" : "Public Tap")
		},
		{
			title: 'Site',
			dataIndex: 'site',
			render: (_, record) => !sitsLoading ? (site[record.data().site] ? site[record.data().site].name : record.data().backupSiteName):(<LoadingOutlined/>)
		},
		{
			title: 'Balance',
			dataIndex: 'balance',
			render: (_, record) => (
						<NumberFormat displayType={'text'} 
							value={unpaidBalance(record)}
							thousandSeparator={true}/>
					)
		},
		{
			title: 'Status',
			dataIndex: 'status',
			render: (_, record) => (
				<Flex alignItems="center">{getUserStatus(record.data().status)}</Flex>
			),
		},
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<EllipsisDropdown menu={dropdownMenu(elm.data())}/>
				</div>
			)
		}
	];

	return (
		<Card>
			<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
				<Flex className="mb-1" mobileFlex={false}>
					<div className="mr-md-3 mb-3">
						<Title level={4}>Milleage</Title>
					</div>
				</Flex>
				<div>
					<Button onClick={addMilleage} size="small" type="primary" icon={<PlusCircleOutlined />} block>New milleage</Button>
				</div>
			</Flex>
			<div className="table-responsive">
				<Table 
					columns={tableColumns} 
					dataSource={!milleagesLoading ? milleages.docs.filter(x => x.data().status) : []} 
					loading={milleagesLoading}
					rowKey='_id' 
				/>
			</div>
		</Card>
	)
}

export default ClientList
