import React, { useState, useEffect } from 'react'
import PageHeaderAlt from 'components/layout-components/PageHeaderAlt'
import { Tabs, Form, Button, message } from 'antd';
import Flex from 'components/shared-components/Flex'
import GeneralField from './GeneralField'
import { useHistory } from "react-router-dom";
import { useCollection } from "react-firebase-hooks/firestore";
import { DataService } from "services/clients.service";
import { DataService as SitesService} from "services/sites.service";
import { DataService as TapsService} from "services/taps.service";
import { DataService as TrucksService} from "services/trucks.service";
import { DataService as EmployeesService} from "services/employees.service";
import { DataService as DestinationsService} from "services/destinations.service";
import LocationData from "assets/data/district.json"
import moment from "moment";

const { TabPane } = Tabs;

const ADD = 'ADD'
const EDIT = 'EDIT'

const MemberForm = props => {

	const { mode = ADD, param, systemData } = props
	const [form] = Form.useForm();
	const [provinceData] = useState(LocationData);
	const [submitLoading, setSubmitLoading] = useState(false)
	const [clients, loading] = useCollection(DataService.getAll());
	const [sites, sitesLoading] = useCollection(SitesService.getAll());
	const [taps, tapsLoading] = useCollection(TapsService.getAll());
	const [trucks, trucksLoading] = useCollection(TrucksService.getAll());
	const [employees, employeesLoading] = useCollection(EmployeesService.getAll());
	const [destinations, destinationsLoading] = useCollection(DestinationsService.getAll());
	const [site, setSite] = useState([]);
	const [drivers, setDrivers] = useState([]);
	const [allTaps, setAllTaps] = useState([]);
	const [currentId, setCurrentId] = useState('');
	const [type, setType] = useState();
	const [others, setOthers] = useState({});
	const [currentPhone, setCurrentPhone] = useState('');
	const [currentMeter, setCurrentMeter] = useState('');
  	const [sitsLoading, setSitsLoading] = useState(true);
  	const [driversLoading, setDriversLoading] = useState(true);
	const { borders, bordersLoading } = systemData;

	const [state, setState] = useState({
		sectorName : '',
		cellName : '',
		villageName : '',
	});
	let history = useHistory();

	if(!sitesLoading){
		let allRecords = [];
	  	let lastSite = '';
		sites.docs.map((doc) => {
		allRecords = {
		  ...allRecords,
		  [doc.data()._id] : {'name': doc.data().name,'siteId': doc.data().siteId,'price': doc.data().price,'myId': doc.data().myId},
		}
		lastSite = doc.data()._id;
		return true;
		})
		if(lastSite && (sitsLoading || !site[lastSite])){
			setSite(allRecords)
			setSitsLoading(false);
		}
	}

	if(!employeesLoading){
		let allRecords = [];
	  	let lastDriver = '';
		employees.docs.map((doc) => {
		allRecords = {
		  ...allRecords,
		  [doc.data()._id] : {'name': doc.data().name,'phoneNumber': doc.data().phoneNumber},
		}
		lastDriver = doc.data()._id;
		return true;
		})
		if(lastDriver && (driversLoading || !drivers[lastDriver])){
			setDrivers(allRecords)
			setDriversLoading(false);
		}
	}

	useEffect(() => {
    	if(mode === EDIT) {
			const { id } = param
			DataService.getRow(id).then(data =>{
				let user = data.data()
				let sDate = user.dateJoined;
				let fullDate=`${new Date(sDate).getFullYear()}/${((new Date(sDate).getMonth())+1) < 10 ? '0' : ''}${(new Date(sDate).getMonth())+1}/${(new Date(sDate).getDate()) < 10 ? '0' : ''}${new Date(sDate).getDate()}`
				form.setFieldsValue({
					name: user.name,
					idNumber: user.idNumber,
					phoneNumber: user.phoneNumber,
					gender: user.gender,
					district: user.district,
					site: user.site,
					type: user.type,
					meter: user.meter,
					dateJoined: moment(fullDate, 'YYYY/MM/DD'),
					sector: provinceData.districts[user.district].sectors[user.sector].name,
					cell: provinceData.districts[user.district].sectors[user.sector].cells[user.cell].name,
					village: provinceData.districts[user.district].sectors[user.sector].cells[user.cell].villages[user.village].name
				});
				setOthers({
					status : user.status ,
					backupUser: user.backupUser,
					userId: user.userId,
					clientId : user.clientId ,
					myId : user.myId ,
				})
				setCurrentId(user.idNumber);
				setType(user.type);
				setCurrentPhone(user.phoneNumber);
				setCurrentMeter(user.meter);
				setState({
					sectorName : provinceData.districts[user.district].sectors[user.sector].id,
					cellName : provinceData.districts[user.district].sectors[user.sector].cells[user.cell].id,
					villageName : provinceData.districts[user.district].sectors[user.sector].cells[user.cell].villages[user.village].id,
				});
			});
		}
  	}, [form, mode, param, props, provinceData]);

	const handleSiteChange = value => {
		let selected = [];
		if(!tapsLoading){
			selected = taps.docs.filter(x => +(x.data().site) === +(value))
		}
		setAllTaps(selected);
	}

	const handleType = value => {
		setType(value);
		form.setFieldsValue({tap:''});
	}

	const onDiscard = () => {
		history.push(`/app/apps/clients`)
	}

	const onFinish = () => {
		let idNumber = form.getFieldValue('idNumber');
		let phoneNumber = form.getFieldValue('phoneNumber');
		let meter = form.getFieldValue('meter');
		const verifyId = (loading || !parseInt(idNumber)) ? [] : clients.docs.filter( employee => employee.data().idNumber === idNumber);
		const verifyMeter = (loading || !parseInt(meter)) ? [] : clients.docs.filter( employee => employee.data().meter === meter);
		const verifyPhone = (loading || !parseInt(phoneNumber)) ? [] : clients.docs.filter( employee => employee.data().phoneNumber === phoneNumber);
		idNumber += '';
		phoneNumber += '';
		meter +='';
		let phoneSliced = phoneNumber.slice(0,2);
		if(parseInt(idNumber) && idNumber.length > 0 && idNumber.length !== 16 && type !== 2){
			message.error('Id number should be 16 numbers '+idNumber);
			return;
		}
		if(parseInt(phoneNumber) && phoneNumber.length > 0 && phoneNumber.length !== 9){
			message.error('Phone number should be 10 numbers');
			return;
		}
		if(parseInt(phoneNumber) && phoneNumber.length > 0 && phoneSliced !== '72' && phoneSliced !== '73' && phoneSliced !== '78' && phoneSliced !== '79'){
			message.error('Invalid phone number');
			return;
		}
		// if(state.sectorName === ""){
		// 	message.error('Select sector');
		// 	return;
		// }
		// if(state.cellName === ""){
		// 	message.error('Select cell');
		// 	return;
		// }
		// if(state.villageName === ""){
		// 	message.error('Select Village');
		// 	return;
		// }
		if(verifyId.length > 0 && idNumber !== ''+currentId){
			message.error('Id Number is already registered');
			return;
		}
		if(verifyMeter.length > 0 && meter !== currentMeter){
			message.error('Water Number is already registered');
			return;
		}
		if(verifyPhone.length > 0 && phoneNumber !== ''+currentPhone){
			message.error('Phone Number is already registered');
			return;
		}
		
		setSubmitLoading(true)
		form.validateFields().then(values => {
			setTimeout(() => {
				let backupSite = {
					backupSiteName:!sitsLoading ? site[values.site].name : '',
					backupSiteSiteId:!sitsLoading ? site[values.site].siteId : '',
					backupSitePrice:!sitsLoading ? site[values.site].price : '',
				}
				let clientData = {
					name : values.name,
					phoneNumber: parseInt(values.phoneNumber) ? values.phoneNumber : '',
					idNumber: parseInt(values.idNumber) ?  values.idNumber : '',
					gender: (type !== 2 && values.gender) ? values.gender : '',
					site: values.site,
					type: values.type,
					meter: (type !== 3 && values.meter) ? values.meter : '',
					tap: (type === 3 && values.tap) ? values.tap : '',
					...backupSite,
					...others,
					district : parseInt(values.district) ? values.district : '',
					sector : parseInt(values.sector) ? values.sector : state.sectorName,
					cell : parseInt(values.cell) ? values.cell : state.cellName,
					village : parseInt(values.village) ? values.village : state.villageName,
					dateJoined : new Date(values.dateJoined.format('YYYY-MM-DD')).setHours(0,0,0,0),
				}
				if(mode === ADD) {
					const { userData } = systemData;
					
					let previous = clients.docs.filter(x => +(x.data().site) === +(values.site)).length;
					let prefix = previous<9?'0':'';
					prefix += previous<99?'0':'';
					prefix += previous<999?'0':'';
					clientData.status = true
					clientData.backupUser=userData.displayName
					clientData.userId=userData.userId
					clientData.clientId = prefix+parseInt(previous+1);
					clientData.myId = parseInt(previous+1);
					DataService.create(clientData).then(result => {
						setSubmitLoading(false)
						if(result)
							message.success(`${values.name} registered to clients list`);
						else
							message.error(`Unable to register, please check your internet connection`);
					})
				}
				if(mode === EDIT) {
					let { id } = param
					DataService.update(id,clientData).then(result => {
						setSubmitLoading(false)
						if(result)
							message.success(`${values.name} updated`);
						else
							message.error(`Unable to update, please check your internet connection`);
					})
				}
			}, 1500);
		}).catch(info => {
			setSubmitLoading(false)
			console.log('info', info)
			message.error('Please enter all required field ');
		});
	};

	return (
		<>
			<Form
				layout="vertical"
				form={form}
				name="advanced_search"
				className="ant-advanced-search-form"
				initialValues={{
					heightUnit: 'cm',
					widthUnit: 'cm',
					weightUnit: 'kg'
				}}
			>
				<PageHeaderAlt className="border-bottom" overlap>
					<div className="container">
						<Flex className="py-2" mobileFlex={false} justifyContent="between" alignItems="center">
							<h2 className="mb-2">{mode === 'ADD'? 'Add New Milleage' : `Edit Milleage`} </h2>
							<div className="mb-3">
								<Button className="mr-2" onClick={() => onDiscard()}>Discard</Button>
								<Button type="primary" onClick={() => onFinish()} htmlType="submit" loading={submitLoading} >
									{mode === 'ADD'? 'Add' : `Save`}
								</Button>
							</div>
						</Flex>
					</div>
				</PageHeaderAlt>
				<div className="container">
					<Tabs defaultActiveKey="1" style={{marginTop: 30}}>
						<TabPane tab="Basic Info" key="1">
							<GeneralField 
								
								initialState={state}
								handleStates={setState}
								systemData={systemData}
								sites={sites}
								sitesLoading={sitesLoading}
								employees={employees}
								employeesLoading={employeesLoading}
								trucks={trucks}
								trucksLoading={trucksLoading}
								destinations={destinations}
								destinationsLoading={destinationsLoading}
								borders={borders}
								bordersLoading={bordersLoading}
								type={type}
								allTaps={allTaps}
								handleSiteChange={handleSiteChange}
								handleType={handleType}
							/>
						</TabPane>
					</Tabs>
				</div>
			</Form>
		</>
	)
}

export default MemberForm
